.title {
  background-color: #000;
  box-shadow: 0 3px 1.6rem 0 rgba(0, 0, 0, 0.7);
  font-weight: bold;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
  position: sticky;
  z-index: 1000;
  top: 0;
}

.right-title {
  justify-content: space-evenly;
  align-items: center;
  gap: 1.6rem;
  margin-right: 1.6rem;
}

.left-title {
  justify-content: space-evenly;
  align-items: center;
  gap: 1.6rem;
  margin-left: 1.6rem;
}

.page-layout {
  display: flex;
  flex-direction: column;
  gap: 0;
  height: 100%;
  width: 100%;
}

@media (max-width: 550px) {
  .title {
    height: 6rem;
  }
  .left-title {
    justify-content: space-evenly;
    align-items: center;
    gap: 1.6rem;
    margin-left: 1.6rem;
    height: 3rem;
  }
}/*# sourceMappingURL=title.css.map */